<template>
  <div>


    <div style="padding: 10px 0">
      <el-input
          v-model="Name"
          placeholder="请输入名称"
          style="width: 200px"
          suffix-icon="el-icon-search"
      ></el-input>
      <el-button style="margin-left: 5px" type="primary" @click="load"
      >搜索</el-button
      >
      <el-button style="margin-left: 5px" type="warning" @click="reset"
      >重置</el-button
      >
    </div>

    <div>
      <el-button style="margin: 5px" type="primary" @click="handleAdd">新增<i class="el-icon-circle-plus"></i></el-button>
      <el-popconfirm
          cancel-button-text="取消"
          class="ml-5"
          confirm-button-text="确定"
          icon="el-icon-info"
          icon-color="red"
          title="确定批量删除这些数据吗"
          @confirm="delBatch"
      >
        <el-button slot="reference" style="margin: 5px" type="danger">批量删除<i class="el-icon-remove"></i></el-button>
      </el-popconfirm>
      <el-upload
          :action="'http://'+serverIP+'/role/import'"  style="display: inline-block"
          :show-file-list="false"
          :headers="headersObj"
          :accept="'xlsx'"
          :on-success="handleExcelSuccess"
      >
        <el-button style="margin: 5px" type="primary" >导入<i class="el-icon-download"></i></el-button>
      </el-upload>

      <el-button style="margin: 5px" type="primary" @click="exp">导出<i class="el-icon-upload2"></i></el-button>
    </div>

    <el-table
        :data="tableData"
        border
        stripe
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="ID" prop="id" width="40"> </el-table-column>
      <el-table-column label="名称" prop="name" width="140">
      </el-table-column>
      <el-table-column label="描述" prop="description" width="140"> </el-table-column>
        <el-table-column label="操作" width="300">
        <template slot-scope="scope">
          <el-button type="info" @click="selectMenu(scope.row)">
            分配菜单
            <i class="el-icon-menu"></i>
          </el-button>
          <el-button type="success" @click="handleEdit(scope.row)"
          >编辑<i class="el-icon-edit-outline"></i
          ></el-button>

          <el-popconfirm
              cancel-button-text="取消"
              class="ml-5"
              confirm-button-text="确定"
              icon="el-icon-info"
              icon-color="red"
              title="确定删除吗"
              @confirm="handleDel(scope.row)"
          >
            <el-button slot="reference" type="danger"
            >删除<i class="el-icon-document-delete"></i
            ></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <div style="padding: 10px 0">
      <el-pagination
          :current-page="pageNum"
          :page-size="pageSize"
          :page-sizes="[2, 5, 10, 20]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handelCurrentChange"
      >
      </el-pagination>
    </div>
    <!--新增窗口-->
    <el-dialog
        :visible.sync="dialogFormVisible"
        title="用户信息"
        width="30%"
    >
      <el-form label-width="80px" size="small">
        <el-form-item label="名称">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="form.description" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog
        :visible.sync="menuDialogVisible"
        title="菜单分配"
        width="40%"
    >
      <el-tree
          :data="menuData"
          :props="props"
          ref="tree"
          :check-strictly="true"
          show-checkbox
          node-key="id"
          :default-expanded-keys="expanded"
          @check-change="handleCheckChange">
        <span class="sutom-tree-node" slot-scope="{ node, data }">
          <span> <i :class="data.icon"></i> {{ data.name }}</span>
      </span>
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveRoleMenu()">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import store from "@/store";
import {serverIP} from "../../public/config";
export default {
  name: "User",
  data(){
    return{
      serverIP:serverIP,
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 10,
      Name: "",
      dialogFormVisible: false,
      menuDialogVisible: false,
      form: {},
      multipleSelection: [],
      menuData: [],
      props:{
        children: 'children',
        label: 'name'
      },
      expanded:[],
      roleId:0,
      roleName:''
    }
  },
  created() {
    //请求分页查询数据
    this.load();
  },
  computed:{
    headersObj(){
      const token = this.user.token
      return {
        token: token
      }
    }
  },
  methods: {
    load() {
      this.request
          .get("/role/page", {
            params: {
              pageNum: this.pageNum,
              pageSize: this.pageSize,
              Name: this.Name,
            },
          })
          .then((res) => {
            console.log(res);
            this.tableData = res.data.records;
            this.total = res.data.total;
          });


    },
    saveRoleMenu(){
      console.log(this.$refs.tree.getCheckedKeys());
      this.request.post("/role-menu/"+this.roleId,this.$refs.tree.getCheckedKeys()).then(res=>{
        if (res.code === '200') {
          this.$message.success("分配成功");
          this.menuDialogVisible=false;
          if(this.roleName===this.user.role){
            store.commit("loginOut");
            this.$message.success("权限发生变化,请重新登录")
          }
        } else {
          this.$message.error(res.msg);
        }
      })
    },
    handleCheckChange(data, checked, indeterminate) {
      console.log(data, checked, indeterminate);
    },
    handleSizeChange(pageSize) {
      console.log(pageSize);
      this.pageSize = pageSize;
      this.load();
    },
    handelCurrentChange(pageNum) {
      console.log(pageNum);
      this.pageNum = pageNum;
      this.load();
    },
    reset() {
      this.Name = "";
      this.load();
    },
    handleAdd() {
      this.form = {};
      this.dialogFormVisible = true;
    },
    save() {
      this.request.post("/role", this.form).then((res) => {
        console.log(this.form);
        if (res.code === '200') {
          this.$message.success(res.msg)
          this.dialogFormVisible = false;
          this.load();
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    handleEdit(row) {
      this.dialogFormVisible = true;
      this.form = row;
    },
    handleDel(row) {
      this.request.delete("/role/" + row.id).then((res) => {
        if (res.code === '200') {
          this.$message.success("删除成功");
          this.load();
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    delBatch() {
      let ids = this.multipleSelection.map((item) => item.id); //将选中的数据的id放入数组中
      console.log(ids);
      this.request.delete("/role/del/batch", {data: ids}).then((res) => {
        //delete 请求对象的data属性是用来传递参数的
        if (res.code === '200') {
          this.$message.success("批量删除成功");
        } else {
          this.$message.error(res.msg);
        }
        this.load();
      });
    },
    exp() {
      window.open(`http://${serverIP}/role/export`);
    },
    handleExcelSuccess(res) {
      if (res.code === '200') {
        this.$message.success("导入成功");
        this.load();
      } else {
        this.$message.error(res.msg);
      }
    },
    selectMenu(row){
      //查询所有菜单
      this.roleId=row.id;
      this.roleName=row.name;
      this.menuDialogVisible=true;
      this.request.get("/menu").then((res) => {
        console.log(res);
        this.menuData = res.data;
        this.expanded = res.data.map(item=>item.id);
      });
      //设置当前角色拥有的菜单
      this.request.get("/role-menu/"+this.roleId).then((res) => {
        console.log(res);
        this.$refs.tree.setCheckedKeys(res.data);
      });
    },
  },
}
</script>

